}

@font-face {
  font-family: Poppins-Bold;
  src: url(/static/media/Poppins-Bold.7940efc4.ttf); 
}

@font-face {
  font-family: Poppins-Medium;
  src: url(/static/media/Poppins-Medium.a4e11dda.ttf); 
}

@font-face {
  font-family: Raleway-Regular;
  src: url(/static/media/Raleway-Regular.580d0778.ttf); 
}

@font-face {
  font-family: Raleway-Black;
  src: url(/static/media/Raleway-Black.3fc090e7.ttf); 
}

